<template>
  <div class="map">
    <div class="filter">
      <div>
        搜索：
        <el-input
          class="input"
          placeholder="请输入地图名或作者名"
          v-model="searchMapName"
        >
        </el-input>
      </div>

      &nbsp; &nbsp;
      <div>
        类型：
        <el-select v-model="form">
          <el-option
            v-for="form in forms"
            :key="form"
            :label="form"
            :value="form"
          >
          </el-option>
        </el-select>
      </div>
      &nbsp; &nbsp;
      <div>
        风格：
        <el-select v-model="style">
          <el-option
            v-for="style in styles"
            :key="style"
            :label="style"
            :value="style"
          >
          </el-option>
        </el-select>
      </div>
      &nbsp; &nbsp;
      <div>
        难度：
        <el-select v-model="difficulty">
          <el-option
            v-for="difficulty in difficulties"
            :key="difficulty"
            :label="difficulty"
            :value="difficulty"
          >
          </el-option>
        </el-select>
      </div>
    </div>
    <el-divider></el-divider>
    <el-table
      class="table"
      stripe
      :data="filteredMaps"
      :row-style="{ height: '65px' }"
    >
      <el-table-column width="100">
        <template slot-scope="scope">
          <el-link type="primary" :href="scope.row.URL">下载</el-link>
        </template>
      </el-table-column>
      <el-table-column label="名称">
        <template slot-scope="scope">
          <el-popover
            class="popover"
            v-if="scope.row.Preview != ''"
            placement="top-start"
            trigger="hover"
            width="600"
          >
            <el-image
              class="image"
              :src="scope.row.Preview"
              :fit="scale - down"
            ></el-image>
            <el-button slot="reference" type="text">
              {{ scope.row.Name }}
            </el-button>
          </el-popover>
          <a v-else>{{ scope.row.Name }}</a>
        </template>
      </el-table-column>
      <el-table-column label="作者">
        <template slot-scope="scope">{{ scope.row.Author }}</template>
      </el-table-column>
      <el-table-column label="大小">
        <template slot-scope="scope">{{ scope.row.Size }}</template>
      </el-table-column>
      <el-table-column label="类型">
        <template slot-scope="scope">{{ scope.row.Form }}</template>
      </el-table-column>
      <el-table-column label="风格">
        <template slot-scope="scope">{{ scope.row.Style }}</template>
      </el-table-column>
      <el-table-column label="难度">
        <template slot-scope="scope">{{ scope.row.Difficulty }}</template>
      </el-table-column>
    </el-table>
  </div>
</template>
  
  <script>
export default {
  data() {
    return {
      maps: [],
      searchMapName: "",
      form: "不限",
      forms: ["不限", "原创", "移植"],
      style: "不限",
      styles: ["不限", "挑战", "休闲"],
      difficulty: "不限",
      difficulties: ["不限", "简单", "中等", "困难", "极难", "地狱"],
    };
  },
  computed: {
    filteredMaps() {
      console.log("change");
      return this.maps.filter(
        (map) =>
          (map.Name.toLowerCase().includes(this.searchMapName.toLowerCase()) ||
            map.Author.toLowerCase().includes(
              this.searchMapName.toLowerCase()
            )) &&
          (this.form === "不限" ||
            map.Form.toLowerCase().includes(this.form.toLowerCase())) &&
          (this.style === "不限" ||
            map.Style.toLowerCase().includes(this.style.toLowerCase())) &&
          (this.difficulty === "不限" ||
            map.Difficulty.toLowerCase().includes(
              this.difficulty.toLowerCase()
            ))
      );
    },
  },
  mounted() {
    this.getSpeedruns();
  },
  methods: {
    async getSpeedruns() {
      const headers = new Headers({
        "X-LC-Id": "3Dec7Zyj4zLNDU0XukGcAYEk-gzGzoHsz",
        "X-LC-Key": "uHF3AdKD4i3RqZB7w1APiFRF",
        "Content-Type": "application/json",
      });
      const options = {
        method: "GET",
        headers: headers,
      };
      let params = {
        order: "Name",
        keys: "Name,Author,URL,Size,Form,Style,Difficulty,Preview,-createdAt,-updatedAt,-objectId",
        //where: JSON.stringify(where),
      };
      const url = `https://3dec7zyj.lc-cn-n1-shared.com/1.1/classes/Map?${new URLSearchParams(
        params
      )}`;
      const response = await fetch(url, options);
      this.maps = (await response.json()).results;
    },
  },
};
</script>
  
    <style scoped>
.table {
  width: 100%;
}
.input {
  width: auto;
}
.filter {
  display: flex;
  flex-wrap: wrap;
  place-items: center;
  justify-content: center;
}
</style>