<template>
  <div class="about">
    <el-dialog :title="dialogTitle" :visible="showDialog">
      <div>
        在审核通过后，您的速通将会出现在排行榜中。
        <br />
        您可以在待审核页面查看您的速通是否处于审核中。
      </div>
      <span slot="footer">
        <el-button type="primary" @click="showDialog = false">确定</el-button>
      </span>
    </el-dialog>
    <el-form
      :hide-required-asterisk="true"
      :model="speedrun"
      :rules="rules"
      ref="speedrun"
      label-width="200px"
      label-position="left"
    >
      <el-form-item label="模式">
        <el-select v-model="speedrun.Category" style="width: 100%">
          <el-option
            v-for="category in categories"
            :key="category.value"
            :label="category.label"
            :value="category.value"
          >
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="游戏平台">
        <el-select v-model="speedrun.Platform" style="width: 100%">
          <el-option
            v-for="platform in platforms"
            :key="platform.value"
            :label="platform.label"
            :value="platform.value"
          >
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="国家">
        <el-select v-model="speedrun.CountryCode" style="width: 100%">
          <el-option
            v-for="countryCode in countryCodes"
            :key="countryCode.value"
            :label="countryCode.label"
            :value="countryCode.value"
          >
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="视频平台">
        <el-select
          v-model="speedrun.VideoPlatform"
          @change="changeVideoPlatform"
          style="width: 100%"
        >
          <el-option
            v-for="videoPlatform in videoPlatforms"
            :key="videoPlatform.value"
            :label="videoPlatform.label"
            :value="videoPlatform.value"
          >
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item
        :label="videoIDName"
        prop="VID"
        :rules="[
          {
            required: true,
            message: `${this.videoIDName}不能为空`,
            trigger: 'blur',
          },
        ]"
      >
        <el-input
          v-model="speedrun.VID"
          :placeholder="`请输入${videoIDName}`"
          style="width: 100%"
        >
        </el-input
      ></el-form-item>
      <el-form-item label="玩家（可选）">
        <el-input
          v-model="speedrun.Player"
          placeholder="请输入玩家名字（仅供审核查看，通过后会改成视频平台用户名）"
          style="width: 100%"
        >
        </el-input
      ></el-form-item>
      <el-form-item label="时间" prop="Time" style="width: 100%">
        <el-row>
          <el-col :span="7">
            <el-input
              v-model.number="speedrun.Minute"
              @input="limitMin"
              style="width: 100%"
            >
            </el-input>
          </el-col>
          <el-col :span="1"> 分 </el-col>
          <el-col :span="7">
            <el-input v-model.number="speedrun.Second" @input="limitSecond">
            </el-input>
          </el-col>
          <el-col :span="1"> . </el-col>
          <el-col :span="7">
            <el-input
              v-model.number="speedrun.MillionSecond"
              @input="limitMillionSecond"
            >
            </el-input>
          </el-col>
          <el-col :span="1"> 秒 </el-col>
        </el-row>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitRun('speedrun')"
          >提交</el-button
        >
      </el-form-item>
    </el-form>
  </div>
</template>
  
  <script>
export default {
  data() {
    return {
      showDialog: false,
      dialogTitle: "提交成功！",
      speedrun: {
        Category: "Glitchless",
        Platform: "PC",
        VideoPlatform: "哔哩哔哩",
        VID: "",
        Player: "",
        CountryCode: "cn",
        Minute: "",
        Second: "",
        MillionSecond: "",
        TotalTime: 0,
      },
      rules: {
        Time: {
          validator: this.checkTime,
          message: "时间不能为空",
          trigger: "blur",
          type: Boolean,
        },
      },
      videoIDName: "哔哩哔哩视频链接（或BV号）",
      categories: [
        {
          value: "Glitchless",
          label: "Glitchless",
        },
        {
          value: "Snake",
          label: "Snake",
        },
      ],
      platforms: [
        {
          value: "PC",
          label: "PC",
        },
        {
          value: "Android",
          label: "安卓",
        },
        {
          value: "IOS",
          label: "IOS",
        },
      ],
      videoPlatforms: [
        {
          value: "哔哩哔哩",
          label: "哔哩哔哩",
        },
        {
          value: "YouTube",
          label: "YouTube",
        },
      ],
      countryCodes: [
        {
          value: "cn",
          label: "中国",
        },
      ],
    };
  },
  mounted() {},
  methods: {
    async createRun(speedrun) {
      const headers = new Headers({
        "X-LC-Id": "3Dec7Zyj4zLNDU0XukGcAYEk-gzGzoHsz",
        "X-LC-Key": "uHF3AdKD4i3RqZB7w1APiFRF",
        "Content-Type": "application/json",
      });
      const options = {
        method: "POST",
        headers: headers,
        body: JSON.stringify(speedrun),
      };
      const url = "https://3dec7zyj.lc-cn-n1-shared.com/1.1/classes/PendingRun";
      const response = await fetch(url, options);
      const result = await response.json();
      console.log(result);
    },
    submitRun(speedrun) {
      this.$refs[speedrun].validate((valid) => {
        if (valid) {
          let found = false;
          switch (this.speedrun.VideoPlatform) {
            case "哔哩哔哩":
              this.speedrun.VID.split("/").forEach((str) => {
                if (str.startsWith("BV")) {
                  found = true;
                  this.speedrun.VID = str;
                }
              });
              break;
            case "YouTube":
              if (this.speedrun.VID.includes("watch?v=")) {
                this.speedrun.VID.split("/").forEach((str) => {
                  if (str.startsWith("watch?v=")) {
                    found = true;
                    this.speedrun.VID = str.substring(8, str.length);
                  }
                });
              }
              break;
          }
          if (!found) {
            alert(`输入的${this.videoIDName}有误，请重新检查。`);
            return;
          }
          this.speedrun.TotalTime =
            this.speedrun.Minute * 60 +
            this.speedrun.Second +
            this.speedrun.MillionSecond / 1000;
          this.speedrun.Time = `${
            this.speedrun.Minute > 0 ? `${this.speedrun.Minute}分` : ""
          }${this.speedrun.Second.toString().padStart(
            2,
            "0"
          )}.${this.speedrun.MillionSecond.toString().padStart(3, "0")}秒`;
          this.createRun(this.speedrun);
          this.showDialog = true;
          this.dialogTitle = "提交成功！";
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    limitMin(val) {
      this.speedrun.Minute = Number(val.replace(/[^\d]/g, ""));
      if (val > 99) {
        this.speedrun.Minute = 99;
      } else if (val < 0) {
        this.speedrun.Minute = 0;
      }
    },
    limitSecond(val) {
      this.speedrun.Second = Number(val.replace(/[^\d]/g, ""));
      if (val > 59) {
        this.speedrun.Second = 59;
      } else if (val < 0) {
        this.speedrun.Second = 0;
      }
    },
    limitMillionSecond(val) {
      this.speedrun.MillionSecond = Number(val.replace(/[^\d]/g, ""));
      if (val > 999) {
        this.speedrun.MillionSecond = 999;
      } else if (val < 0) {
        this.speedrun.MillionSecond = 0;
      }
    },
    changeVideoPlatform() {
      console.log(this.speedrun.VideoPlatform);
      switch (this.speedrun.VideoPlatform) {
        case "哔哩哔哩":
          this.videoIDName = "哔哩哔哩视频链接（或BV号）";
          break;
        case "YouTube":
          this.videoIDName = "YouTube视频链接";
          break;
      }
    },
    checkTime(rule, value, callback) {
      if (
        this.speedrun.Minute.length <= 0 ||
        this.speedrun.Second.length <= 0 ||
        this.speedrun.MillionSecond.length <= 0
      ) {
        return callback(new Error("时间不能为空"));
      } else callback();
    },
  },
};
</script>
  